"use client";

import { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../context/AuthContext";
import { ThemeContext } from "../../context/ThemeContext";
import { useRouter } from "next/navigation";
import dynamic from "next/dynamic";
import { FaPlus, FaUserFriends, FaChevronLeft, FaChevronRight } from "react-icons/fa";

// Load emoji picker dynamically
const Picker = dynamic(() => import("emoji-picker-react"), { ssr: false });

export default function ProfilePage() {
  const { user } = useContext(AuthContext);
  const { theme } = useContext(ThemeContext);
  const router = useRouter();

  // States
  const [showForm, setShowForm] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [showFriendRequests, setShowFriendRequests] = useState(false);
  const [showMyFriends, setShowMyFriends] = useState(false);
  const [suggestedUsers, setSuggestedUsers] = useState([]);
  const [friendRequests, setFriendRequests] = useState([]);
  const [myFriends, setMyFriends] = useState([]);

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [imageFile, setImageFile] = useState(null);
  const [videoFile, setVideoFile] = useState(null);
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const [message, setMessage] = useState("");
  const [collapsed, setCollapsed] = useState(false);

  // Redirect if not logged in
  useEffect(() => {
    if (!user) router.push("/login");
    else {
      fetchSuggestions();
      fetchFriendRequests();
    }
  }, [user]);

  if (!user) return null;

  // Helper to show temporary messages
  const showTemporaryMessage = (msg) => {
    setMessage(msg);
    setTimeout(() => setMessage(""), 3000);
  };

  // Fetch friend suggestions
  const fetchSuggestions = async () => {
    try {
      const res = await fetch("/api/friends/suggestions");
      const data = await res.json();
      setSuggestedUsers(data.users || []);
    } catch (err) {
      console.error(err);
    }
  };

  // Fetch incoming friend requests
  const fetchFriendRequests = async () => {
    try {
      const res = await fetch("/api/friends/requests");
      const data = await res.json();
      setFriendRequests(data.requests || []);
    } catch (err) {
      console.error(err);
    }
  };

  // Fetch my friends
  const fetchMyFriends = async () => {
    try {
      const res = await fetch("/api/friends/list");
      const data = await res.json();
      setMyFriends(data.friends || []);
    } catch (err) {
      console.error(err);
    }
  };

  // Toggle friend suggestions panel
  const handleAddFriendsClick = async () => {
    setShowSuggestions(!showSuggestions);
    if (!showSuggestions && suggestedUsers.length === 0) await fetchSuggestions();
  };

  // Toggle friend requests panel
  const handleShowFriendRequests = async () => {
    setShowFriendRequests(!showFriendRequests);
    if (!showFriendRequests) await fetchFriendRequests();
  };

  // Toggle My Friends panel
  const handleShowMyFriends = async () => {
    setShowMyFriends(!showMyFriends);
    if (!showMyFriends) await fetchMyFriends();
  };

  // Send friend request
  const handleSendRequest = async (friendId) => {
    try {
      const res = await fetch("/api/friends/add", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ friendId }),
      });
      const data = await res.json();
      if (res.ok) {
        setSuggestedUsers(prev => prev.filter(u => u.id !== friendId));
        showTemporaryMessage("Friend request sent!");
      } else showTemporaryMessage(data.error || "Failed to send request");
    } catch (err) {
      console.error(err);
      showTemporaryMessage("Failed to send request");
    }
  };

  // Accept friend request
  const handleAcceptRequest = async (requestId) => {
    try {
      const res = await fetch("/api/friends/accept", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ requestId }),
      });
      if (res.ok) {
        setFriendRequests(prev => prev.filter(r => r.id !== requestId));
        showTemporaryMessage("Friend request accepted!");
      }
    } catch (err) {
      console.error(err);
      showTemporaryMessage("Failed to accept request");
    }
  };

  // Reject friend request
  const handleRejectRequest = async (requestId) => {
    try {
      const res = await fetch("/api/friends/reject", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ requestId }),
      });
      if (res.ok) {
        setFriendRequests(prev => prev.filter(r => r.id !== requestId));
        showTemporaryMessage("Friend request rejected!");
      }
    } catch (err) {
      console.error(err);
      showTemporaryMessage("Failed to reject request");
    }
  };

  // Create post
  const handleCreatePost = async (e) => {
    e.preventDefault();
    if (!title && !content && !imageFile && !videoFile) return alert("Add some content!");

    const formData = new FormData();
    formData.append("title", title);
    formData.append("content", content);
    if (imageFile) formData.append("image", imageFile);
    if (videoFile) formData.append("video", videoFile);

    try {
      const res = await fetch("/api/posts/create", { method: "POST", body: formData });
      const data = await res.json();
      if (res.ok) {
        setTitle(""); setContent(""); setImageFile(null); setVideoFile(null); setShowForm(false);
        showTemporaryMessage("Post created successfully!");
      } else showTemporaryMessage(data.error || "Something went wrong");
    } catch (err) {
      console.error(err);
      showTemporaryMessage("Failed to create post");
    }
  };

  const onEmojiClick = (emojiData) => {
    setContent(prev => prev + emojiData.emoji);
  };

  // Dynamic styles
  const styles = {
    page: { display: "flex", minHeight: "100vh", background: theme === "dark" ? "#121212" : "#fff", color: theme === "dark" ? "#f1f1f1" : "#000" },
    sidebar: { width: collapsed ? "70px" : "220px", transition: "width 0.3s", padding: "20px 10px", background: theme === "dark" ? "#1e1e1e" : "#f0f0f0", display: "flex", flexDirection: "column", gap: "15px", borderRight: theme === "dark" ? "1px solid #333" : "1px solid #ccc", position: "sticky", top: 0, height: "100vh" },
    sidebarBtn: { display: "flex", alignItems: "center", gap: collapsed ? "0" : "10px", padding: "10px", background: theme === "dark" ? "#333" : "#ddd", color: theme === "dark" ? "#fff" : "#000", border: "none", borderRadius: "6px", cursor: "pointer", width: "100%", justifyContent: collapsed ? "center" : "flex-start" },
    collapseBtn: { marginTop: "auto", padding: "6px", background: "transparent", border: "none", cursor: "pointer", color: theme === "dark" ? "#fff" : "#000", display: "flex", justifyContent: collapsed ? "center" : "flex-end" },
    main: { flex: 1, padding: "30px" },
    card: { marginBottom: "20px", padding: "20px", background: theme === "dark" ? "#1e1e1e" : "#eee", borderRadius: "8px" },
    form: { marginTop: "20px", padding: "20px", background: theme === "dark" ? "#1e1e1e" : "#f7f7f7", borderRadius: "8px", display: "flex", flexDirection: "column", gap: "10px" },
    input: { padding: "10px", border: "1px solid #ccc", borderRadius: "5px", background: theme === "dark" ? "#2b2b2b" : "#fff", color: theme === "dark" ? "#f1f1f1" : "#000" },
    textarea: { padding: "10px", border: "1px solid #ccc", borderRadius: "5px", background: theme === "dark" ? "#2b2b2b" : "#fff", color: theme === "dark" ? "#f1f1f1" : "#000" },
    emojiBtn: { padding: "6px 12px", background: "#ffecb3", borderRadius: "5px", border: "none", cursor: "pointer", width: "fit-content" },
    submitBtn: { padding: "6px 12px", background: "green", color: "white", borderRadius: "6px", border: "none", cursor: "pointer" },
    message: { marginTop: "15px", color: "green" },
    suggestionCard: { marginTop: "15px", padding: "10px", background: theme === "dark" ? "#2a2a2a" : "#eee", borderRadius: "6px", display: "flex", justifyContent: "space-between", alignItems: "center" },
  };

  return (
    <div style={styles.page}>
      {/* Sidebar */}
      <div style={styles.sidebar}>
        <button onClick={() => setShowForm(!showForm)} style={styles.sidebarBtn}>
          <FaPlus /> {!collapsed && <span>Create Post</span>}
        </button>

        <button onClick={handleAddFriendsClick} style={styles.sidebarBtn}>
          <FaUserFriends /> {!collapsed && <span>Friend Suggestions</span>}
        </button>

        <button onClick={handleShowFriendRequests} style={styles.sidebarBtn}>
          <FaUserFriends /> {!collapsed && <span>Friend Requests</span>}
        </button>

        <button onClick={handleShowMyFriends} style={styles.sidebarBtn}>
          <FaUserFriends /> {!collapsed && <span>My Friends</span>}
        </button>

        <button onClick={() => setCollapsed(!collapsed)} style={styles.collapseBtn}>
          {collapsed ? <FaChevronRight /> : <FaChevronLeft />}
        </button>
      </div>

      {/* Main Content */}
      <div style={styles.main}>
        <h1>Your Profile</h1>

        {/* User Info */}
        <div style={styles.card}>
          <p><strong>Name:</strong> {user.name || "No Name"}</p>
          <p><strong>Email:</strong> {user.email}</p>
          <p><strong>Role:</strong> {user.role}</p>
          <p><strong>Joined:</strong> {new Date(user.createdAt).toLocaleDateString()}</p>
        </div>

        {/* Create Post Form */}
        {showForm && (
          <form onSubmit={handleCreatePost} style={styles.form} encType="multipart/form-data">
            <input type="text" placeholder="Post title" value={title} onChange={e => setTitle(e.target.value)} style={styles.input} />
            <textarea placeholder="Write your post..." value={content} onChange={e => setContent(e.target.value)} rows={4} style={styles.textarea} />
            <button type="button" onClick={() => setShowEmojiPicker(!showEmojiPicker)} style={styles.emojiBtn}>
              {showEmojiPicker ? "Close Emojis" : "Add Emoji"}
            </button>
            {showEmojiPicker && <Picker onEmojiClick={onEmojiClick} theme={theme === "dark" ? "dark" : "light"} />}
            <div style={{ display: "flex", gap: "10px", flexDirection: "column" }}>
              <input type="file" accept="image/*" onChange={e => setImageFile(e.target.files[0])} />
              <input type="file" accept="video/*" onChange={e => setVideoFile(e.target.files[0])} />
            </div>
            <button type="submit" style={styles.submitBtn}>Publish</button>
          </form>
        )}

        {/* Message */}
        {message && <p style={styles.message}>{message}</p>}

        {/* Friend Suggestions Panel */}
        {showSuggestions && (
          <div style={{ marginTop: "20px" }}>
            <h2>Friend Suggestions</h2>
            {suggestedUsers.length === 0 ? <p>No suggestions found</p> :
              suggestedUsers.map(u => (
                <div key={u.id} style={styles.suggestionCard}>
                  <span>{u.name || u.email}</span>
                  <button onClick={() => handleSendRequest(u.id)} style={styles.submitBtn}>Send Request</button>
                </div>
              ))
            }
          </div>
        )}

        {/* Friend Requests Panel */}
        {showFriendRequests && (
          <div style={{ marginTop: "20px" }}>
            <h2>Incoming Friend Requests</h2>
            {friendRequests.length === 0 ? <p>No requests</p> :
              friendRequests.map(r => (
                <div key={r.id} style={styles.suggestionCard}>
                  <span>{r.fromName || "Unknown User"}</span>
                  <div style={{ display: "flex", gap: "5px" }}>
                    <button onClick={() => handleAcceptRequest(r.id)} style={styles.submitBtn}>Accept</button>
                    <button onClick={() => handleRejectRequest(r.id)} style={{ ...styles.submitBtn, background: "red" }}>Reject</button>
                  </div>
                </div>
              ))
            }
          </div>
        )}

        {/* My Friends Panel */}
        {showMyFriends && (
          <div style={{ marginTop: "20px" }}>
            <h2>My Friends</h2>
            {myFriends.length === 0 ? <p>You have no friends yet</p> :
              myFriends.map(f => (
                <div key={f.id} style={styles.suggestionCard}>
                  <span>{f.friend.name || f.friend.email}</span>
                </div>
              ))
            }
          </div>
        )}
      </div>
    </div>
  );
}
